body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  -ms-overflow-style: none;
	overflow: -moz-scrollbars-none;
}

body::-webkit-scrollbar {
  width: 0 !important
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
	width: 100vw;
	height: 100vh;
}

body {
	background-color: lightgrey;
}



.StatusBar {
	width: 100%;
	height: 50px;
	background-color: darkgrey;
	text-align: center;
}



#arrow {
	height: 10px;
	width: 10px;
	cursor: pointer;
}

#empty {
	display: inline-block;
	width: 10%;
}

#statusBarText {
	display: inline-block;
	color: black;
	font-size: 24pt;
	width: 80%;
}

#Arrow {
	display: inline-block;
	width: 10%;
}

.flipped {
	transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
}


.invisible {
	display: none;
}
.EditCardPopup {
    height: 450px;
    width: 75%;
	background: white;
	border-radius: 30px;
	right: 12.5%;
	top: 108px;
}

#title {
	font-size: 24pt;
}

#submit {
	height: 30px;
	width: 30%;
	border: transparent;
	background-color: pink;
	border-radius: 10px;
	font-size: 16pt;
	color: navy;
}


.Card {
	background-color: white;
	width: 190px;
	height: 200px;
	display: inline-block;
	border-radius: 20px;
	text-align: center;
	margin: 5px;
	cursor: default;
}

#Header {
	border-bottom: 1px solid grey;
	height: 25px;
	overflow: hidden;
}

.edit {
	width: 20px;
	height: 20px;
	margin-top: 2.5px;
	margin-right: 150px;
}

.Title {
	margin-left: 30px;
	margin-top: -25px;
	-webkit-text-emphasis: bold;
	        text-emphasis: bold;
	width: 130px;
	height: 25px;
}

.trash {
	position: absolute;
	margin-left: 65px;
	margin-top: -22.5px;
	width: 20px;
	height: 20px;
	cursor: pointer;
}

.Body {
	height: 150px;
	font-size: 10pt;
	width: 190px;
	overflow-y: scroll;
	overflow-wrap: break-word;
	-ms-overflow-style: none;
	overflow: -moz-scrollbars-none;
	border-bottom: 1px solid grey;
}

.Body::-webkit-scrollbar {
  width: 0 !important
}

.Footer {
	font-size: 9pt;
	color: grey;
}

#EditCardPopup {
	display: block;
}


.Row {
	width: 100%;
	height: 210px;
	text-align: center;
	background-color: grey;
}


.CardHolder {
	width: 100%;
}

.empty {
	background-color: grey;
	height: 40px;
	text-align: center;
	color: white;
	font-size: 18pt;
}


.TopBar {
	background-color: lightgrey;
	width: 100%;
	height: 108px;
}

table {
	width: 100%;
	height: 108px;
	border: transparent;
	border-collapse: collapse;
}

td#Logo {
	width: 25%;
	height: 108px;
}

td#Title {
	text-align: center;
	width: 50%;
	font-size: 30pt;
	font-family: 'Segoe UI', sans-serif;
	font-weight: bold;
}

td#AddCard {
	width: 25%;
}

#logo {
	height: 108px;
	display: block;
	border-radius: 10px;
}


.AddCardPopup {
    position: absolute;
    display: block;
    height: 450px;
    margin-top: 48px;
    margin-left: -62.5%;
    width: 75%;
	background: white;
	border-radius: 30px;
}

#title {
	font-size: 24pt;
}

#submit {
	height: 30px;
	width: 30%;
	border: transparent;
	background-color: pink;
	border-radius: 10px;
	font-size: 16pt;
	color: navy;
}


.AddCard {
	width: 100%;
	height: 100%;
	text-align: center;
}

#openDio {
	margin-top: 30px;
	height: 40px;
	width: 65%;
	border: transparent;
	background-color: pink;
	border-radius: 10px;
	font-size: 16pt;
	color: navy;
	outline: 0 none;
}

.invisible {
	display: none;
}
